import React from "react"
import PropTypes from "prop-types"
import { withRouter } from "react-router-dom"
import { Dropdown, Menu, Container } from "semantic-ui-react"
import "./Header.css"
import translate from "../translate"
import LANG from "./Header.lang"
import Logo from "./Logo.svg"

export function Header(props) {
  const { lang } = props

  function getPath(lang) {
    const { location, history } = props

    // split url into root, lang, rest of path
    const [, , ...rest] = location.pathname.split("/")

    return ["", lang, ...rest].join("/")
  }

  const enPath = getPath("en")
  const jpPath = getPath("jp")

  return (
    <Menu stackable borderless>
      <Container>
        <Menu.Item header as="a" href="/">
          <img src={Logo} alt="logo" />
          <span className="Header-brand">SMART CONTRACT PROGRAMMER</span>
        </Menu.Item>

        <Menu.Menu position="right">
          <Menu.Item name="blog" as="a" href={`/${lang}/blog`}>
            {translate(LANG, lang, "Blog")}
          </Menu.Item>
          <Menu.Item name="contact" as="a" href={`/${lang}/contact`}>
            {translate(LANG, lang, "Contact")}
          </Menu.Item>
          {lang == "en" ? (
            <Menu.Item name="contact" as="a" href={jpPath}>
              Japanese
            </Menu.Item>
          ) : (
            <Menu.Item name="contact" as="a" href={enPath}>
              English
            </Menu.Item>
          )}
        </Menu.Menu>
      </Container>
    </Menu>
  )
}

Header.propTypes = {
  lang: PropTypes.oneOf(["en", "jp"]).isRequired,
  location: PropTypes.shape({
    pathname: PropTypes.string.isRequired,
  }).isRequired,
}

export default withRouter(Header)
